@import "_normalize";
@import "_variables";
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap");
@import "_mixins";
html {
    box-sizing: border-box;
    font-size: 16px;
    @media (max-width: 800px) {
        font-size: 14px;
    }
    @media (max-width:600px) {
        font-size: 11px;
    }
}

*,
*:before,
*:after {
    box-sizing: inherit;
}

body {
    color: $darkblue;
    background: $darkblue;
    min-height: 100vh;
    font-family: "poppins";
}

.main {
    margin: 0.5rem;
    background-color: white;
    @media (max-width: 600px) {
        margin: 0;
    }
    &__header {
        @include header;
    }
}

.about {
    max-width: 780px;
    padding-top: 5rem;
    padding-left: 5rem;
    @media (max-width: 600px) {
        padding-left: 5px;
    }
    &__image {
        width: 110px;
        height: 110px;
        border: 4px solid #c4c4c4;
        border-radius: 55px;
        padding: 5px;
        margin-bottom: 32px;
    }
    &__paragraph {
        @include paragraph;
        padding-right: 100px;
        margin-top: 16px;
        margin-bottom: 60px;
    }
    &__mail {
        @include link;
        border-bottom: 2px solid #0593fb;
        padding-bottom: 16px;
        display: inline;
    }
}

.skills {
    padding-left: 5rem;
    padding-right: 5rem;
    max-width: 1184px;
    margin-top: 12.5rem;
    @media (max-width: 600px) {
        padding-left: 5px;
        padding-right: 5px;
    }
    &__wrapper {
        display: grid;
        grid-template-columns: 1fr 2fr;
        row-gap: 120px;
        padding-top: 128px;
        border-top: 2px solid #c4c4c4;
        @media (max-width: 800px) {
            display: block;
            padding-top: 84px;
        }
    }
    &__header {
        @media (max-width: 600px) {
            text-align: center;
        }
    }
    &__list {
        columns: 100px 2;
        list-style-image: url(../assets/img/Vector4.svg);
        @media (max-width: 800px) {
            columns: unset;
            padding-left: 10px;
            margin-bottom: 50px;
        }
        &--purple {
            columns: 100px 2;
            list-style-image: url(../assets/img/vector2.svg);
            @media (max-width: 800px) {
                columns: unset;
                padding-left: 10px;
                margin-bottom: 50px;
            }
        }
        &--blue {
            columns: 100px 2;
            list-style-image: url(../assets/img/Vector3.svg);
            @media (max-width: 800px) {
                columns: unset;
                padding-left: 10px;
                margin-bottom: 50px;
            }
        }
    }
    &__item {
        @include paragraph;
        padding-left: 14px;
        margin-left: 1rem;
    }
}

.green__border {
    @include border;
    border-bottom: 4px solid #06c847;
}

.purple__border {
    @include border;
    border-bottom: 4px solid #653cad;
}

.blue__border {
    @include border;
    border-bottom: 4px solid #2251cc;
    &-footer {
        @include border;
        border-bottom: 4px solid #2251cc;
    }
}

.projects {
    padding-top: 8rem;
    padding-left: 5rem;
    margin-top: 10.5rem;
    background: #f5f7fa;
    @media (max-width: 600px) {
        padding-left: 5px;
        padding-right: 5px;
    }
    @media (max-width:800px) {
        margin-top: 7rem;
    }
    &__header {
        @media (max-width: 600px) {
            text-align: center;
        }
    }
    &__paragraph {
        @include paragraph;
        max-width: 580px;
        margin-bottom: 4rem;
        @media (max-width: 600px) {
            text-align: center;
        }
    }
    &__wrapper {
        display: grid;
        grid-template-columns: 1fr 1fr;
        column-gap: 24px;
        row-gap: 24px;
        max-width: 1184px;
        padding-right: 1.5rem;
        padding-bottom: 1.5rem;
        @media (max-width: 800px) {
            display: block;
        }
        @media (max-width: 600px) {
            padding: 10px;
        }
    }
    &__item {
        max-width: 580px;
        box-shadow: 0px 12px 12px rgba(0, 0, 0, 0.25);
        border-bottom-left-radius: 8px;
        border-bottom-right-radius: 8px;
        @media (max-width: 800px) {
            margin: 20px 0 20px 0;
        }
        &--header {
            text-transform: capitalize;
        }
        &--content {
            background-color: #ffffff;
            border: 1px solid #ffffff;
            border-top-left-radius: 8px;
            border-top-right-radius: 8px;
            padding: 2.5rem;
            min-height: 260px;
            @media (max-width: 860px) {
                font-size: 14px;
            }
        }
    }
    &__footer {
        display: flex;
        justify-content: flex-end;
        align-items: center;
        background-color: #f5f7fa;
        border: 1px solid #f5f7fa;
        border-bottom-left-radius: 8px;
        border-bottom-right-radius: 8px;
        padding: 8px;
        &--link {
            margin: 8px 24px 8px 8px;
            color: #0593fb;
            text-decoration: none;
            &-demo {
                padding-right: 24px;
                border-right: 1px solid #cbd2d9;
            }
        }
    }
}

.footer {
    margin-top: 0;
    background-color: $darkblue;
    color: white;
    padding: 5rem;
    @media (max-width: 600px) {
        padding-left: 5px;
    }
    &__header {
        margin-top: 1.75rem;
        font-weight: 600;
    }
    &__links {
        margin-bottom: 3.125rem;
    }
    &__link {
        margin-right: 20px;
    }
}